.case-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    padding-top: 40px;
    height: 100vh;
    width: 100vw;
    color: #fff;
    text-align: center;
    box-sizing: border-box;
    overflow-y: auto;
    overflow-x: hidden;
    background: none;
}
  
.tabs-container {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}
  
.tab {
    flex: 1;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: color 0.3s;
    padding: 10px;
    border-radius: 8px;
    background-color: #3b2a4d;
    color: #b3a1d1;
    margin: 0 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}
  
.tab:hover {
    color: #fff;
}
  
.tab.active {
    color: #fff;
}
  
.case-info {
    display: flex;
    align-items: center;
    background: linear-gradient(145deg, rgba(59, 42, 77, 0.5), rgba(42, 29, 58, 0.5));
    color: #fff;
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
    position: relative;
    z-index: 2;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}
  
.case-info-icon {
    width: 50px;
    height: 50px;
}
  
.case-info-text {
    font-size: 20px;
    font-weight: bold;
    margin-left: 10px;
    color: #fff;
    position: relative;
}
  
.case-container-wrapper {
    border-radius: 15px;
    padding: 10px;
    width: 90%;
    margin-top: 10px;
}
  
.case-container {
    display: flex;
    justify-content: center;
    margin-top: 10px;
}
  
.case {
    width: 150px;
    height: 150px;
    background-size: cover;
    background-position: center;
    border: none;
    border-radius: 15px;
    position: relative;
    transition: transform 0.3s, box-shadow 0.3s;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}
  
.case:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.5);
}
  
.case-cooldown {
    filter: grayscale(100%);
    pointer-events: none;
}
  
.cooldown-timer {
    position: absolute;
    top: 5px;
    left: 5px;
    background: rgba(0, 0, 0, 0.7);
    color: white;
    padding: 2px 5px;
    border-radius: 5px;
    font-size: 12px;
}
  
.case-cost {
    position: absolute;
    bottom: 5px;
    right: 5px;
    background: linear-gradient(145deg, rgba(59, 42, 77, 0.5), rgba(42, 29, 58, 0.5));
    color: black;
    padding: 2px 5px;
    border-radius: 12%;
    font-size: 12px;
    display: flex;
    align-items: center;
}
  
.coin-icon {
    width: 12px;
    height: 12px;
    margin-right: 2px;
}
  
.case-details-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(10px);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    color: white;
    text-align: center;
    padding: 20px;
    box-sizing: border-box;
}
  
.case-details {
    background: rgba(0, 0, 0, 0.6);
    border-radius: 15px;
    padding: 20px;
    text-align: center;
    color: #fff;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
    position: relative;
}
  
.case-details img {
    width: 90px;
    height: 90px;
    border-radius: 15px;
    border: 2px solid #fff;
    padding: 5px;
    background: #000;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}
  
.case-details ul {
    list-style: none;
    padding: 0;
    margin: 10px 0;
}
  
.case-details ul li {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    font-size: 16px;
}
  
.case-details ul li img {
    width: 50px;
    height: 50px;
    border-radius: 10px;
    margin-right: 10px;
}
  
.case-details button {
    margin-top: 10px;
    padding: 12px 25px;
    background: #3b2a4d; /* Стили кнопок */
    color: #fff;
    border: none;
    font-size: 16px;
    font-weight: bold;
    border-radius: 25px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}
  
.case-details button:hover {
    background: #2a1d3a;
    transform: scale(1.05);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.5);
}
  
.modal-buttons {
    display: flex;
    justify-content: space-between;
    gap: 10px;
}
  
.case-opening-animation {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.85);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    color: white;
    text-align: center;
    padding: 20px;
    box-sizing: border-box;
}
  
.scroll-container {
    position: relative;
    width: 100%;
    overflow: hidden;
}
  
.scroll-items {
    display: flex;
    will-change: transform;
    animation-fill-mode: forwards; /* Удерживает последний кадр анимации */
    animation-timing-function: cubic-bezier(0.4, 0.0, 0.2, 1); /* Быстрый старт, медленное завершение */
}
  
.scroll-item {
    width: 100px;
    height: 100px;
    margin-right: 10px;
}
  
.cursor {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 10px;
    height: 100px;
    background-color: red;
    z-index: 1001;
}
  
.reward-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.85);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    color: white;
    text-align: center;
    padding: 20px;
    box-sizing: border-box;
}
  
.reward-details {
    background: rgba(0, 0, 0, 0.6);
    border-radius: 15px;
    padding: 20px;
    text-align: center;
    color: #fff;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
}
  
.error-message {
    color: red;
    font-size: 16px;
    font-weight: bold;
    margin-top: 10px;
}
  
.inventory-button {
    background-color: rgba(0, 0, 0, 0.85);
    color: #fff;
    padding: 10px 20px;
    border-radius: 10px;
    border: none;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    margin-bottom: 20px;
    position: relative;
}
