/* inventory.css */
.inventory-container {
  background: linear-gradient(145deg, rgba(59, 42, 77, 0.5), rgba(42, 29, 58, 0.5));
  border-radius: 15px;
  padding: 20px;
  text-align: center;
  font-weight: bold;
  color: #fff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  position: relative;
  max-width: 500px;
  width: 100%;
  margin: 20px auto; /* Center align the inventory container */
}

.inventory-container .close-button {
  display: none; /* Hide the close button */
}

.inventory-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
}

.inventory-item {
  background: rgba(0, 0, 0, 0.7);
  border-radius: 10px;
  padding: 10px;
  margin: 10px;
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.inventory-item img {
  width: 100px;
  height: 100px;
}

.inventory-item p {
  margin-top: 10px;
  font-size: 16px;
}

.inventory-item.selected {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.5);
}

.sell-container {
  margin-top: 20px;
}

.sell-button {
  padding: 12px 25px;
  background: #3b2a4d;
  color: #fff;
  border: none;
  font-size: 16px;
  font-weight: bold;
  border-radius: 25px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.sell-button:hover {
  background: #2a1d3a;
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.5);
}
